    border-radius: 15px;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
    display: flex;
  }

  .our-products__img {
    width: 14rem;
    height: auto;
  }

  .our-products__card-title {
    text-transform: uppercase;
    letter-spacing: -.02rem;
    margin: 0 0 2px;
    font-size: 1.125rem;
    font-weight: 600;
    line-height: 1.5rem;
  }

  .our-products__card-desc {
    color: var(--color-description);
    letter-spacing: -.02rem;
    margin: 0 0 1.125rem;
    font-size: .75rem;
    font-weight: 400;
    line-height: .875rem;
  }

  .our-products__card-btn {
    width: 5.875rem;
    height: 2.063rem;
    border: 1px solid var(--color-borders);
    background-color: var(--color-pearl);
    color: var(--color-coal);
    border-radius: 6.25rem;
    font-size: 14px;
    font-weight: 600;
    line-height: 17.07px;
  }

  .our-products__ico-bar {
    width: 1.75rem;
    height: .875rem;
  }

  .our-products__ico {
    fill: var(--color-borders);
    width: .875rem;
    height: .875rem;
  }

  .our-products__scroll-bar {
    margin: 1.25rem auto;
  }
}

